.fade-in-linear-enter-active,
.fade-in-linear-leave-active {
  transition: opacity 200ms linear; }

.fade-in-linear-enter,
.fade-in-linear-leave,
.fade-in-linear-leave-active {
  opacity: 0; }

/** tu-fade-in-linear */
.tu-fade-in-easeout-enter-active,
.tu-fade-in-easeout-leave-active {
  transition: opacity 0.3s ease-out; }

.tu-fade-in-easeout-enter,
.tu-fade-in-easeout-leave,
.tu-fade-in-easeout-leave-active {
  opacity: 0; }

/** tu-fade-in */
.tu-fade-in-enter-active,
.tu-fade-in-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-fade-in-enter,
.tu-fade-in-leave-active {
  opacity: 0; }

/** tu-popper-in-center */
.tu-popper-in-center-enter-active,
.tu-popper-in-center-leave-active {
  transition: all 0.2s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-popper-in-center-enter,
.tu-popper-in-center-leave-active {
  scale: 0;
  transform: scaleX(0); }

/** tu-zoom-in-center */
.tu-zoom-in-center-enter-active,
.tu-zoom-in-center-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-zoom-in-center-enter,
.tu-zoom-in-center-leave-active {
  opacity: 0;
  transform: scaleX(0); }

/** tu-zoom-in-top */
.tu-zoom-in-top-enter-active,
.tu-zoom-in-top-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center top; }

.tu-zoom-in-top-enter,
.tu-zoom-in-top-leave-active {
  opacity: 0;
  transform: scaleY(0); }

.tu-zoom-in-bottom-enter-active,
.tu-zoom-in-bottom-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center bottom; }

.tu-zoom-in-bottom-enter,
.tu-zoom-in-bottom-leave-active {
  opacity: 0;
  transform: scaleY(0); }

/** tu-zoom-in-left */
.tu-zoom-in-left-enter-active,
.tu-zoom-in-left-leave-active {
  opacity: 1;
  transform: scale(1, 1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: top left; }

.tu-zoom-in-left-enter,
.tu-zoom-in-left-leave-active {
  opacity: 0;
  transform: scale(0.45, 0.45); }

.collapse-transition {
  transition: 0.3s height ease-in-out, 0.3s padding-top ease-in-out, 0.3s padding-bottom ease-in-out; }

.horizontal-collapse-transition {
  transition: 0.3s width ease-in-out, 0.3s padding-left ease-in-out, 0.3s padding-right ease-in-out; }

.tu-list-enter-active,
.tu-list-leave-active {
  transition: all 0.48s; }

.tu-list-enter,
.tu-list-leave-active {
  opacity: 0;
  transform: translateX(-40px); }

.tu-opacity-transition {
  transition: opacity 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

html,
body {
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

body {
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  color: #9baacf;
  -webkit-font-smoothing: antialiased; }

/* BEM [block]__[element]--[modifier]
 -------------------------- */
.tu-dropdown {
  display: inline-block;
  position: relative;
  color: #8394be;
  font-size: 14px; }
  .tu-dropdown .tu-dropdown__caret-button {
    padding-left: 5px;
    padding-right: 5px;
    position: relative;
    border-left: none; }
    .tu-dropdown .tu-dropdown__caret-button::before {
      content: "";
      position: absolute;
      display: block;
      width: 1px;
      top: 5px;
      bottom: 5px;
      left: 0; }
    .tu-dropdown .tu-dropdown__caret-button:hover:not(.is-disabled)::before {
      top: 0;
      bottom: 0; }
    .tu-dropdown .tu-dropdown__caret-button .tu-dropdown__icon {
      padding-left: 0; }
  .tu-dropdown__icon {
    margin: 0 3px; }
  .tu-dropdown .tu-dropdown-selfdefine:focus:active, .tu-dropdown .tu-dropdown-selfdefine:focus:not(.focusing) {
    outline-width: 0; }
  .tu-dropdown [disabled] {
    cursor: not-allowed;
    color: #bec8e4; }

.tu-dropdown-menu {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  padding: 10px 0;
  margin: 5px 0;
  border-radius: 4px;
  box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
  .tu-dropdown-menu__item {
    list-style: none;
    line-height: 36px;
    padding: 0 20px;
    margin: 0;
    font-size: 14px;
    color: #8394be;
    cursor: pointer;
    outline: none;
    transition: all 0.3s; }
    .tu-dropdown-menu__item:not(.is-disabled):hover, .tu-dropdown-menu__item:focus {
      background-color: rgba(200, 208, 231, 0.38);
      color: #6d5dfc; }
    .tu-dropdown-menu__item i {
      margin-right: 5px; }
    .tu-dropdown-menu__item--divided {
      position: relative;
      margin-top: 6px; }
      .tu-dropdown-menu__item--divided::after {
        content: "";
        display: block;
        position: absolute;
        left: 0;
        top: -6px;
        width: 100%;
        border-top: 1px solid rgba(131, 148, 190, 0.25); }
    .tu-dropdown-menu__item.is-disabled {
      cursor: not-allowed;
      color: #bec8e4; }
  .tu-dropdown-menu--medium {
    padding: 6px 0; }
    .tu-dropdown-menu--medium .tu-dropdown-menu__item {
      line-height: 30px;
      padding: 0 17px;
      font-size: 14px; }
      .tu-dropdown-menu--medium .tu-dropdown-menu__item.tu-dropdown-menu__item--divided {
        margin-top: 6px; }
        .tu-dropdown-menu--medium .tu-dropdown-menu__item.tu-dropdown-menu__item--divided:before {
          height: 6px;
          margin: 0 -17px; }
  .tu-dropdown-menu--small {
    padding: 6px 0; }
    .tu-dropdown-menu--small .tu-dropdown-menu__item {
      line-height: 27px;
      padding: 0 15px;
      font-size: 13px; }
      .tu-dropdown-menu--small .tu-dropdown-menu__item.tu-dropdown-menu__item--divided {
        margin-top: 4px; }
        .tu-dropdown-menu--small .tu-dropdown-menu__item.tu-dropdown-menu__item--divided:before {
          height: 4px;
          margin: 0 -15px; }
  .tu-dropdown-menu--mini {
    padding: 3px 0; }
    .tu-dropdown-menu--mini .tu-dropdown-menu__item {
      line-height: 24px;
      padding: 0 10px;
      font-size: 12px; }
      .tu-dropdown-menu--mini .tu-dropdown-menu__item.tu-dropdown-menu__item--divided {
        margin-top: 3px; }
        .tu-dropdown-menu--mini .tu-dropdown-menu__item.tu-dropdown-menu__item--divided:before {
          height: 3px;
          margin: 0 -10px; }
